*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-size: 10px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.text{
  width: 100%;
  line-height: 3rem;
  font-size: 2.5rem;
}

.date{
  display: inline-block;
  width: 50%;
  line-height: 2.5rem;
  font-size: 2rem;
}

div.important{
  display: inline-block;
  width: 100%;
  /* padding-left:10px; */
  line-height: 2.5rem;
  /* text-align: left; */
}

.important label{
  font-size: 2rem;
  padding-right: 5px;
}

.addTask{
  display: inline-block;
  width: 100%;
  line-height: 2.5rem;
  font-size: 2rem;
}

.taskList , .taskListFinished{
    width: 100%;
    
    line-height: 3rem;
    display: block;
}

.taskList h2 , .taskListFinished h2{
    
    font-size: 2rem;
}

.taskListFinished p strong , .taskList p strong{
    font-size: 2rem;
}

.taskListFinished p , .taskList p{
    font-size: 1.5rem;
}
.App{
  min-width: 360px;
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}
